:root {
  --color-primary: #3CA115;
  --color-secondary: #012244;
}


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;500;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*::selection {
  background-color: var(--color-secondary);
  color: #fff !important;
}

html {
  font-family: 'Inter', sans-serif;
  font-size: 62.5% !important; // 1rem = 10px, 10px/16px = 62.5%
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  scroll-behavior: smooth;
  line-height: 1.5;
  color: #545454;
  width: 100%;
  overflow-x: hidden;
}

body {
  font-family: 'Inter', sans-serif;
  background-repeat: no-repeat;
  min-height: 100vh;
  max-width: 100%;
  overflow-x: hidden !important;
}

.Navbar {
  background: #FFFFFF;
  padding: .5rem 0 !important;

  &_image {
    margin-right: 5rem;
    margin-left: 5rem;

    @media only screen and (max-width: 1600px) {
      margin: 0;
    }

    &-img {
      width: 17rem;
      max-width: 100% !important;
      height: 12.7rem;
      margin-left: 2rem;
      padding: .5rem 0;

      @media only screen and (max-width: 1600px) {
        width: 22rem;
        margin-left: 2rem;
      }

      @media only screen and (max-width: 1300px) {
        width: 33rem;
        margin-left: 0;
      }

      @media only screen and (max-width: 990px) {
        width: 17rem;
        max-width: 100% !important;
        height: 12.7rem;
        margin-left: 2rem;
        padding: 0;
      }

      @media only screen and (max-width: 768px) {
        width: auto;
        height: 8vh;
        max-width: 100%;
        margin-top: 1rem !important;
        margin-bottom: 1rem;
      }
    }

  }

  &_nav {
    background-color: #fff !important;
    font-size: 1.3rem !important;
    display: flex !important;
    justify-content: space-between !important;
    transition: all 5s fade-in;


    &-item {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      max-width: 100%;
      margin: 0 2rem;

      @media only screen and (max-width: 1300px) {
       margin: 0 .5rem;
      }

      @media only screen and (max-width: 768px) {
        justify-content: flex-start !important;
        align-items: flex-start !important;
        text-align: left;
      }
    }

    &-link {
      font-family: 'Arial';
      font-style: normal;
      font-weight: 700;
      font-size: 2.2rem;
      line-height: 1.6;
      text-align: center;
      color: #031E42;
      white-space: nowrap;

      @media only screen and (max-width: 768px) {
        margin: .5rem 0 !important;
      }

      &:hover {
        color: var(--color-secondary) !important;
        cursor: pointer !important;
      }
    }

    &-btn{
      font-family: 'Inter';
      font-style: normal;
      font-weight: 700;
      font-size: 2.3rem;
      line-height: 1.7;
      color: #FFFFFF;
      background: #3CA115;
      padding: 1rem 2.5rem !important;
      text-align: center;
      border: none;
      border-radius: 2.9rem;
      margin: 0 4rem;

      @media only screen and (max-width: 900px) {
        margin-left: 0 !important;
      }

      &:hover{
        text-decoration: none;
        background: var(--color-secondary);
        color: #fff;
        transition: all .5s;
        cursor: pointer;
      }
    }
  }
}

.navbar-active .Navbar_image-img{
  width: 17rem;
  max-width: 100% !important;
  height: 10rem;
  padding: 0 !important;
}

.navbar-active {
  position: fixed !important;
  width: 100%;
  max-width: 100%;
  background-color: #fff !important;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.12);
  border: 0 !important;
  top: 0 !important;
  left: 0 !important;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  transition: 0.2s;
  z-index: 1000 !important;

  .navbar{
      height: 10vh;
  }
}

.slider{
  position: relative;
  background-image: url("../img/slider.png");
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  height: 80rem;
  z-index: 0;

  &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #000;
      opacity: 0.3;
      z-index: -1;
      transition: 0.5s;
  }

  &_content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 10rem;
    padding-left: 15rem;
    width: 90rem;

    @media only screen and (max-width: 990px) {
      width: 100%;
      padding-left: .5rem;
      padding-right: .5rem;
    }


    &-top{
      font-family: 'Inter';
      font-style: normal;
      font-weight: 700;
      font-size: 2.5rem;
      line-height: 1.7;
      color: #FFFFFF;
      margin-bottom: 4rem;

      @media only screen and (max-width: 990px) {
        text-align: center;
      }
    }

    &-title{
      font-family: 'Inter';
      font-style: normal;
      font-weight: 700;
      font-size: 5.5rem;
      line-height: 1.7;
      color: #FFFFFF;

      @media only screen and (max-width: 990px) {
        text-align: center;
      }
    }

    &-divider{
      width: 14.8rem;
      padding: .1rem 0;
      color: var(--color-primary);
      background: var(--color-primary);
      margin-bottom: 2rem;

      @media only screen and (max-width: 990px) {
        margin: 0 auto;
      }
    }

    &-p{
      font-family: 'Inter';
      font-style: normal;
      font-weight: 400;
      font-size: 2.5rem;
      line-height: 1.6;
      text-align: justify;
      color: #FFFFFF;
      padding-top: 2rem;

      @media only screen and (max-width: 990px) {
        text-align: center;
      }
    }
  }
}

