  padding: 5rem 0;
  padding-bottom: 10rem;

  &_box{
    background: #FFFFFF;

    @media only screen and (max-width: 990px) {
      margin: 2rem 0;
    }

    &-content{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 6rem 0;

      &--icon{
        color: #000;
        font-size: 8rem;
        margin-bottom: 4rem;
      }

      &--link{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 3rem;
        line-height: 1.7;
        text-align: center;
        color: #3CA115;

        &:hover{
          text-decoration: none;
          color: var(--color-secondary);
          cursor: pointer;
        }
      }
    }
  }
}

.link{

  &:hover{
    text-decoration: none;
    cursor: pointer;
  }
}

// Imports
@import "bootstrap";


.dropdown.active {
  a.dropdown-item{
    color: $body-color!important;
    &.active{
      color: $white!important;
    }
  }
}
